.txt_color1 {
  color: rgb(234, 0, 255);
}

#bg_color1 {
  background: #ccc;
}

/* 배경이미지 */
.bg_img {
  width: 342px;
  height: 228px;
  background: url('../images/banner2.png');
}

.num {
  color: #333;
  font-weight: 600;
  font-size: 16px;
  padding: 0px 15px;
}

.btn {
  border: none;
  background: #fff;
  width: 24px;
  /* align-items: center; */
  padding: 5px 0px;
  font-size: 20px;
}

/* 마켓컬리 */
.box {
  border: 3px solid #1f1c1c;
  padding: 5px;
  width: 600px;
}

.top {
  border: 1px solid #ebe8e8;
  padding: 10px;
}

.box_top {
  display: flex;
  justify-content: space-between;
  padding: 10px 0px;

}

.box_top_left {
  border: 1px solid #ccc;
  width: 100px;
  text-align: center;

}

.total_price {
  font-size: 30px;
  font-weight: bold;
}

.cart {
  width: 590px;
  display: block;
  text-align: center;
  background: purple;
  margin: 0 auto;
  border-radius: 5px;
  margin-top: 20px;
  ;
}

a {
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  padding: 15px 0px;
}

.bottom {
  padding: 10px;
  text-align: right;

}
