* {
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "맑은고딕", arial, sans-serif;
  font-size: 16px;
  color: #333;
}

.header {
  background: rgb(84, 84, 194);
  text-align: center;
  position: fixed;
  top:0px; left:0px;
  z-index: 999;
  width:100%;
}

.header>h1 {
  color: #fff;
  font-size: 30px;
  padding:16px 0px;
}
p{line-height: 40px;}

.gnb{
  position: fixed;
  top:0px;right:-100%;
  background: rgba(0,0,0,.8);
  z-index: 2000;
  width:100%;height:100vh;
  transition:0.3s;
}
.close_btn{
  border:none;
  position:absolute;
  top:0px;right:0px;
  z-index:2001;
  color:#333;
  padding:10px;
  cursor:pointer;
  background: #fff;
  height:80px;
  width:300px;
  text-align: right;

}
.gnb_list{
  position: absolute;
  top:80px;right:0px;
  list-style: none;
  background: #fff;
  width:300px;height:100vh;
}
.gnb_list li button{
  border:none;
  border-bottom: 1px solid #ccc;
  padding:10px 10%;
  text-align: left;
  width:100%;
  background: #fff;
  color:#333;
}
.gnb_list li:last-child button{
  border:none;
}
/* 메인서식 */
main {
  padding: 65px 10px;
}

.list01 {
  margin: 10px 0px 15px 30px;
}

.list01 li {
  line-height: 34px;
}

main .title2 {
  background: rgb(67, 134, 76);
  color: #fff;
  line-height: 30px;
  margin: 14px 0px;
}

.list01 ul,
.list01 li ol {
  margin-left: 16px;
  background: #e7e7e7;
}

main .title3 {
  color: #0c8059;
  margin-bottom: 10px 0px;
}

main .title4 {
  line-height: 54px;
}


.footer {
  text-align: center;
  line-height: 40px;
  background: #333;
}

.footer>address {
  color: #fff;
  font-style: normal;
}